@font-face {
  font-family: Avenir Book;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

:root {
  --text-color: #3d4e61;
  --default-weight: 300;
  --default-font: "Avenir Book", sans-serif;
  --accent-font: "Avenir Heavy", sans-serif;
}

.page {
  color: #3d4e61;
  font-family: Avenir Book, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.page__form {
  margin-top: 20px;
}

.search-bar {
  box-sizing: border-box;
  background-image: url("search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  align-items: center;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar--big {
  background-position: 26px;
  background-size: 19px 19px;
  height: 70px;
  padding-left: 62px;
}

.search-bar--small {
  background-position: 13px;
  background-size: 11px 11px;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.search-bar__input {
  font-family: var(--default-font);
  font-weight: var(--default-weight);
  color: var(--text-color);
  font-size: inherit;
  border: none;
  outline: none;
  flex-grow: 1;
  padding: 0;
}

.search-bar--small .search-bar__input {
  font-weight: 400;
}

.search-bar__input::placeholder {
  font-family: var(--default-font);
  font-weight: var(--default-weight);
  color: var(--text-color);
  font-size: inherit;
}

.search-bar:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar:focus {
  filter: drop-shadow(0 4px 4px #00000040);
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
}

.search-bar__input:focus {
  font-family: var(--accent-font);
}
